:root {
  --blue: #2980b9;
  --light-blue: #ecf4f9;
  --border: 0.1rem solid rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  box-sizing: border-box;
  text-decoration: none;
  text-transform: capitalize;
  font-weight: normal;
  font-family: "Josefin Sans", sans-serif;
}

.continer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 2rem 7rem 2rem;
  background: #f0f3f5;
  min-height: 100vh;
}

.continer form {
  width: 60rem;
  box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-align: center;
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
}
.continer form .links {
  background: #fff;
  padding: 3rem 1.5rem;
}

.continer form .title {
  font-size: 1.7rem;
  padding-bottom: 1.5rem;
  color: #666;
}

.continer form .links .buttons .btn {
  font-size: 1.7rem;
  margin: 0.5rem;
  display: inline-block;
  background: #ecf4f9;
  background: var(--light-blue);
  border: 0.1rem solid rgba(0, 0, 0, 0.1);
  border: var(--border);
  padding: 1rem 3rem;
  color: #333;
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
}

.continer form .links .buttons .btn i {
  color: #333;
  padding-right: 0.5rem;
}

.continer form .links .buttons .btn:hover {
  color: #fff;
  background: #2980b9;
  background: var(--blue);
}

.continer form .links .buttons .btn:hover i {
  color: #fff;
}

.continer form .input-box {
  padding: 3rem;
  background: #ecf4f9;
  background: var(--light-blue);
}

.continer form .input-box .input {
  margin: 1.5rem auto;
  display: flex;
  align-items: center;
  height: 5rem;
  width: 100%;
  background: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border: 0.1rem solid rgba(0, 0, 0, 0.1);
  border: var(--border);
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
}

.continer form .input-box .input input {
  height: 100%;
  width: 100%;
  padding: 1rem;
  font-size: 1.6rem;
  color: #333;
  text-transform: none;
}

.continer form .input-box .input span {
  color: #999;
  padding: 0 1.5rem 0 1rem;
  font-size: 2rem;
}

.continer form .checkbox {
  text-align: left;
  padding: 1rem 0;
}

.continer form .checkbox label {
  font-size: 1.7rem;
  color: #333;
  cursor: pointer;
  padding-left: 0.5rem;
}

.continer form .signInButton {
  padding: 1rem 4rem;
  margin-top: 1rem;
  font-size: 1.7rem;
  color: #fff;
  background: #2980b9;
  background: var(--blue);
  cursor: pointer;
  transition: 0.2 all;
  -webkit-transition: 0.2 all;
  -moz-transition: 0.2 all;
  -ms-transition: 0.2 all;
  -o-transition: 0.2 all;
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
  -ms-border-radius: 0.5rem;
  -o-border-radius: 0.5rem;
}

.continer form .signInButton:hover {
  letter-spacing: 0.2rem;
}

